import React, { Component } from 'react';
import Habits from './habits'

class Nav extends Component {

  render() {

    return (
    
      <nav className = "habit-nav">
        <h2 className = "habit-nav-title"><i class="fas fa-leaf"></i>   Habit Tracker</h2>
        <span className = "habit-countAll">{this.props.totalCount}</span>
      </nav>
    
    );
  }
}



export default Nav;